<template>
  <div>
      <v-dialog
            max-width="400"
            persistent
            v-model="deleteModal"
        >
            <v-card class="aksessuar_add">

                <div class="aksessuar_add__title">
                    <span class="text-black">{{modalTitle}}</span>
                    <v-btn icon @click="closeDeleteModal">
                        <img src="@/assets/images/BackButton.svg" alt="BackButton">
                    </v-btn>
                </div>

                <div class="pb-5 pt-4 mb-3 text-muted border-bottom">
                    {{modalDesc}}
                </div>

                <v-card-actions class="d-flex justify-space-between">
                    <v-btn
                        text
                        class="backBtn"
                        elevation="0"
                        @click="closeDeleteModal"
                    >Yoq
                    </v-btn>
                    <v-btn
                        class="primaryBtn"
                        elevation="0"
                        :loading="loading"
                        @click="removeProduct()"
                    >Ha
                    </v-btn>
                </v-card-actions>

            </v-card>
        </v-dialog>
  </div>
</template>

<script>
import {mapState, mapActions} from "vuex";
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'

export default {
    mixins: [loaderMixin],
    props: ['deleteModal', 'user_id', 'actionName', 'modalTitle', 'modalDesc'],
    data() {
        return {

        }
    },
    computed: {
        ...mapState(
        {
            // product: state => state.code.product,
        }),
        routeNAME: {
            get: function() {
                return this.$store.state.main.routeNAME
            },
            set: function(newValue) {
                return this.$store.state.main.routeNAME = newValue
            }
        },
    },
    methods: {
        ...mapActions(
        {
            
        }),
        closeDeleteModal(){
            this.$parent.deleteModal = false
        },
        async removeProduct() {
            this.loader = 'loading'
            let abc = await this.$store.dispatch(this.actionName, this.user_id)

            if(abc.response && abc.response.status == 409){
                if(abc.response.data.message){
                    this.errorMessage = abc.response.data.message;
                }
                this.$toast.error(this.errorMessage);
            }else{

                switch(this.actionName){
                    case 'main/deleteUser':
                        this.$store.commit('main/SET_UPDATE_USERS_LIST', true);
                        break;
                    case 'warehouse/deleteStorage':
                        if(this.routeNAME == 'warehouse-ip-mavjud'){
                            this.$store.commit('main/SET_UPDATE_WAREHOUSE_THREAD_EXISTS', true);
                        }else if(this.routeNAME == 'warehouse-mato-mavjud'){
                            this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_EXISTS', true);
                        }else if(this.routeNAME == 'warehouse-yoqa-mavjud'){
                            this.$store.commit('main/SET_UPDATE_WAREHOUSE_COLLAR_EXISTS', true);
                        }else if(this.routeNAME == 'supply-ready-mato-mavjud'){
                            this.$store.commit('main/SET_UPDATE_SUPPLY_READY_RAW_MATERIAL_EXISTS', true);
                        }else if(this.routeNAME == 'supply-ready-yoqa-mavjud'){
                            this.$store.commit('main/SET_UPDATE_SUPPLY_READY_RAW_COLLAR_EXISTS', true);
                        }else if(this.routeNAME == 'supply-aksessuar-mavjud'){
                            this.$store.commit('main/SET_UPDATE_SUPPLY_ACCESSORY_EXISTS', true);
                        }else if(this.routeNAME == 'supply-bichuv-skladi'){
                            this.$store.commit('main/SET_UPDATE_SUPPLY_TAILORING_WAREHOUSE', true);
                        }else if(this.routeNAME == 'ready-products-mavjud'){
                            this.$store.commit('main/SET_UPDATE_READY_PRODUCTS_EXISTS', true);
                        }

                        break;
                        
                    case 'warehouse/deleteProductIncome':
                        if(this.routeNAME == 'warehouse-ip-kirim'){
                            this.$store.commit('main/SET_UPDATE_WAREHOUSE_THREAD_INCOME', true);
                        }else if(this.routeNAME == 'supply-aksessuar-kirim'){
                            this.$store.commit('main/SET_UPDATE_SUPPLY_ACCESSORY_INCOME', true);
                        }
                        break;

                    case 'warehouse/deleteOrder':
                        if(this.routeNAME == 'warehouse-mato-chiqim'){
                            this.$store.commit('main/SET_UPDATE_WAREHOUSE_MATERIAL_OUTCOME', true);
                        }else if(this.routeNAME == 'warehouse-yoqa-chiqim'){
                            this.$store.commit('main/SET_UPDATE_WAREHOUSE_COLLAR_OUTCOME', true);
                        }else if(this.routeNAME == 'outsource-painting-kirim'){
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_PAINTING_INCOME', true);
                        }
                        break;

                    case 'updateDelete/deleteFactoryIncome': 
                        if(this.routeNAME == 'warehouse-ip-chiqim'){
                            this.$store.commit('main/SET_UPDATE_WAREHOUSE_THREAD_OUTCOME', true);
                        }else if(this.routeNAME == 'outsource-mato-kirim'){
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_MATERIAL_INCOME', true);
                        }else if(this.routeNAME == 'outsource-yoqa-kirim'){
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_COLLAR_INCOME', true);
                        }
                        break;

                    case 'updateDelete/deleteOrderSewed': 
                        if(this.routeNAME == 'outsource-mato-buyurtmalarim'){
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_MATERIAL_ORDERS', true);
                        }else if(this.routeNAME == 'outsource-yoqa-buyurtmalarim'){
                            this.$store.commit('main/SET_UPDATE_OUTSOURCE_COLLAR_ORDERS', true);
                        }
                        break;

                    case 'updateDelete/deleteHistoryDeleteSewed': 
                        if(this.routeNAME == 'supply-bichuv-tarixi'){
                            this.$store.commit('main/SET_UPDATE_SUPPLY_TAILORING_HISTORY', true);
                        }
                        break;
                
                }
                this.closeDeleteModal()
                let toastTitle = 'Muvaffaqiyatli o\'chirildi!'
                this.$toast.success(toastTitle);
            }
        }
    },
   
}
</script>

<style scoped>

