        <DeleteModal :deleteModal="deleteModal"></DeleteModal> -->
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import NoData from "@/components/base/NoData.vue";
// import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
// import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin],
    components:{
        // DeleteModal,
        NoData
    },
    data(){
        return {
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            loading: false,
            headers: [
                {text: 'Qayerga', value: 'direction', align: 'start', class: 'table-header'},
                {text: 'Kodi', value: 'code', align: 'center', class: 'table-header'},
                {text: 'Nomi', value: 'name', align: 'center', class: 'table-header'},
                {text: 'Massa', value: 'amount', align: 'center', class: 'table-header'},
                {text: 'Rangi', value: 'color_code', align: 'center', class: 'table-header'},
                {text: 'Sana', value: 'date', align: 'center', class: 'table-header'},
                // {text: 'Harakatlar', value: 'actions', align: 'center', class: 'table-header'},
            ],
            items: [],
        }
    },
    watch: {
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        '$store.state.main.supply_ready_raw_collar_outcome': {
            handler: async function(value) {
                if(value){
                    await this.refresh()
                    this.$store.commit('main/SET_UPDATE_SUPPLY_READY_RAW_COLLAR_OUTCOME', false)
                }
            },
            deep: true,
            immediate: true
        },
    },
    async mounted() {
        await this.refresh();
    },
    computed: {
        ...mapState({
            order_extra_product: state => state.supply.order_extra_product,
            searchValue: state => state.main.searchValue,
                dateFilterValue: state => state.main.dateFilterValue
        })
    },
    methods: {
        ...mapActions({
            fetchOrderExtraProduct: 'supply/fetchOrderExtraProduct'
        }),
        async refresh() {
            this.loading = true;
            await this.fetchOrderExtraProduct({page: this.page, parent_id: 3, search: this.searchValue, date: this.dateFilterValue})
            this.page = this.order_extra_product.current_page;
            this.pageCount = Math.ceil(this.order_extra_product.total / 10);
            this.items = this.order_extra_product.data;

            this.items.forEach((elem) => {
                elem.direction = elem.order.factory.name
                elem.type = elem.product.product_category.name
                elem.name = elem.product.name
                elem.code = elem.product.code
                elem.color_code = elem.order.color_code
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY');
            })
            this.loading = false;
        },
        closeEditModal(){
            this.editModal = false
        },
    }
}
</script>

