<template>
<div>
    <v-dialog
        v-model="modal"
        persistent
        width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
            <v-text-field
                dense
                v-model="dateRangeText"
                placeholder="Sana tanlang"
                append-icon="mdi-calendar"
                readonly
                outlined
                v-bind="attrs"
                v-on="on"
                hide-details
            ></v-text-field>
        </template>
        <v-date-picker
            v-model="dates"
            scrollable
            range
            color="blue lighten-1"
            locale="ru-ru"
            first-day-of-week="1"
            selected-items-text="2ta Tanlandi"
        >
        <v-card-actions class="d-flex justify-space-between btn-pickers">
            <v-btn
                class="backBtn "
                elevation="0"
                @click="closeDialog"
            >O`chirish
            </v-btn>
            <v-btn
                class="primaryBtn"
                elevation="0"
                @click="FilterByDate()"
            >Filtirlash
            </v-btn>
        </v-card-actions>
        </v-date-picker>
    </v-dialog>
</div>
</template>

<script>
import { routesMixin } from '@/mixins/routeNames'

export default {
    mixins: [routesMixin],
    data: () => ({
        dates: [],
        modal: false,
    }),
    computed: {
        dateRangeText () {
            return this.dates.join('~')
        },
        dateFilterValue: {
            get: function() {
                return this.$store.state.main.dateFilterValue
            },
            set: function(newValue) {
                return this.$store.state.main.dateFilterValue = newValue
            }
        },
        searchValue: {
            get: function() {
                return this.$store.state.main.searchValue
            },
            set: function(newValue) {
                return this.$store.state.main.searchValue = newValue
            }
        },
    },
    methods: {
        closeDialog(){
            this.modal = false;
            this.dates = [];

            this.dateFilterValue = '';
            
            this.routeNames();


        },
        async FilterByDate(){
            this.dateFilterValue = this.dates[0] + ',' + this.dates[1]

            this.searchValue = await 'Start our mission';
            this.searchValue = await ''

            this.modal = false;
        }
    }
}
</script>

